<template>
    <div>
         <!-- 顶部导航栏 -->
         <van-nav-bar title="私人FM">
              <template #left>
                 <span class="iconfont icon-arrow-fine-up-copy" @click.stop=""></span>
              </template>
         </van-nav-bar>
        
        <img  v-lazy="">
       <div></div>
    </div>
</template>

<script>
    export default {
        name : 'Fm',
        data() {
            return {
                fmList : [],
                fmObj  : {},
            }
        },
        created(){ 
            // 请求fm数据
            this.ff_fm();
        },
        methods:{
            // 请求fm数据
            ff_fm(){
                this.axios({
                    url : '/personal_fm'
                })
                .then(res=>{
                    if(res.data.code == 200){
                        
                        res.data.data.map(item=>{
                            this.fmList.push(item)
                        })
                        this.fmObj = this.fmList[0];
                        console.log(this.fmList)

                    }
                })
                .catch(err=>{
                    console.log(err)
                })
            }
        }
    }
</script>

